	module.exports = {"pathContext":{"posts":[{"html":"<h2>Terminology</h2>\n<ul>\n<li>kubernetes / k8s / kube: the whole orchestration system</li>\n<li>kubeclt aka cube control: cli to configure kubernetes and manage apps</li>\n<li>node: single server in the k8s cluster</li>\n<li>kubelet: k8s agent running on nodes</li>\n<li>each kubelet can have a kube-proxy controlling its networking</li>\n<li>control plane aka the \"master\": set of containers that manage the cluster. Includes api server, scheduler, controller manager, etcd, core DNS and more</li>\n</ul>\n<h2>Links</h2>\n<p>Kubernetes in a browser:  </p>\n<p>try <a href=\"http://play-with-k8s.com\">http://play-with-k8s.com</a>\nor <a href=\"katacoda.com\">katacoda.com</a> in browser</p>\n<p>Local install:  </p>\n<ul>\n<li>\n<p>use snap for install on linux </p>\n</li>\n<li>\n<p>control the microK8s service via microk8s. commands</p>\n</li>\n<li>\n<p>kubectl accessible wia microk8s.kubectl</p>\n</li>\n<li>\n<p>add an alias to your shell (.bash_profile): </p>\n<ul>\n<li>alias kubectl=microk8s.kubectl</li>\n</ul>\n</li>\n</ul>","id":"/home/nicolas/projects/workshop/src/pages/2021-11-25-kubernetes/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2021-11-25T10:17:00.823Z","path":"/kubernetes","title":"Kubernetes","excerpt":"","tags":["kubernetes","cloud","devops"]}}],"tagName":"kubernetes"}}

